          <div class="contact_text" id="intro">  <h2>Introduction</h2> </div>
      
        <div> <hr class="separation_vign"> </div>
        <div class="contact_text" id="intro_text">
          <div class="intro-container">
            <div class="intro-text">
              <p>White adipose tissue plays a pivotal role in energy storage and release. 
              Disruptions in these processes are marked by disturbances at the transcriptional level 
              and are linked to cardiometabolic diseases. Multiple efforts have therefore investigated
              adipose tissue function through transcriptional analyses. However, the resulting data remain 
              scattered, lacking a simple and unified approach for exploration. To address this, we provide 
              the adipose tissue knowledge portal (ATportal) where we have systematically
              collected, generated, re-analyzed, and integrated both transcriptomic and proteomic data from diverse
              clinical cohorts, different adipose-residing cell types and perturbation studies of human
              adipocytes. Together, our open-access framework
              allows integrative analyses and can be used as a discovery tool to uncover new aspects of adipose biology.</p>
              
              <p>The <router-link to="/summary">Summary</router-link> is a perfect starting point for exploring the ATportal with a gene of interest.
              In contrast, the <router-link to="/genefinder"> Gene Finder</router-link> allows identifying transcripts fitting user-specified traits.
              Currently, six additional modules further allow a more detailed view on <router-link to="/clinical"> clinical correlations</router-link> in over 6000 individuals,
              <router-link to="/depots">depot differences</router-link>, <router-link to="/celltype">cell type differences and adipogenesis</router-link>, 
              adipose tissue at the <router-link to="/singlecell">single cell</router-link> and <router-link to="/spatial">spatial</router-link> resolution 
              as well as various <router-link to="/perturbation">perturbations</router-link> of in vitro cultured adipocytes.</p>
              
              <p>The code for this portal is available via GitHub, and we invite everyone to contribute to this portal by providing new data sets, or by improving current code.</p>
            </div>
            <div class="intro-image">
              <img src="@/assets/images/logo_navbar_2.png" alt="Introduction Image">
            </div>
          </div>
        </div>

